/* today techcloud login.bundle.css */

body[frappe-session-status="logged-out"][data-path="login"] .navbar.navbar-light.navbar-expand-lg{
  display: none;
}

body[frappe-session-status="logged-out"][data-path="login"] .web-footer{
  display: none;
}

body[frappe-session-status="logged-out"][data-path="login"] .page_content{
  display: flex;
}

body[frappe-session-status="logged-out"][data-path="login"] .container{
  display: flex !important;
  height: 85vh !important;
  width: 100% !important;
  justify-content: end !important;
  background: white !important;
}

body[frappe-session-status="logged-out"][data-path="login"] .container.my-4{
  margin-block : 40px !important;
  overflow: hidden !important;
  border-radius: 26px !important;
}

@media (min-width: 992px) {
    .container {
        padding-left: unset !important;
        padding-right: unset !important;
    }
}

body[frappe-session-status="logged-out"][data-path="login"] .page_content{
  background: green;
  flex: 1 !important;
}

body[frappe-session-status="logged-out"][data-path="login"] .page-header-wrapper{
  background-position: center !important;
  background: transparent !important;
  flex: 1 !important;
  position: relative !important;
  overflow: hidden !important;
  background: url(/assets/techcloud/images/Art2.png) !important;
  height: 100%;
  width: 100%;
  background-size: cover !important;
}
body[frappe-session-status="logged-out"][data-path="login"] .page-header-wrapper .page-header
{
}

@media(max-width : 767px){
  .page-header-wrapper{
    display: none !important;
  }
}

input:-webkit-autofill {
  background-color: #ffffff !important; /* your desired background */
  -webkit-box-shadow: 0 0 0px 1000px #ffffff inset !important; /* forces background */
  -webkit-text-fill-color: #000000 !important; /* text color */
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s; /* prevent flashing */
}
