body[frappe-session-status="logged-out"][data-path="login"] .page_content>div{
  display: flex;
  background: whitesmoke;
  width: 100%;
  align-items: center;
  justify-content: center;
}

body[frappe-session-status="logged-out"][data-path="login"] .for-login,
body[frappe-session-status="logged-out"][data-path="login"]  .for-forgot,
body[frappe-session-status="logged-out"][data-path="login"]  .for-login-with-email-link{
  width: 80% !important;
  padding-inline: 2em !important;
}

body[frappe-session-status="logged-out"][data-path="login"] .page-card-head .app-logo{
  content: url(/assets/techcloud/images/techcloudlogo.svg);
  width: 200px !important;
  height: auto !important;
}


body[frappe-session-status="logged-out"][data-path="login"] .email-field,
body[frappe-session-status="logged-out"][data-path="login"] .password-field {
  display: flex !important;
  align-items: center !important;
  justify-content: space-between !important;
  width: 100% !important;
  margin-inline: auto !important;
  border: 1px solid #ddd;
  border-radius: 6px;
  padding: 6px 10px;
  background: #fff;
  flex-direction: row-reverse;
  position: relative;
}

body[frappe-session-status="logged-out"][data-path="login"] .password-field .custom-toggle-password{
  position: absolute;
  right: 10px;
}

body[frappe-session-status="logged-out"][data-path="login"] .email-field input,
body[frappe-session-status="logged-out"][data-path="login"] .password-field input {
  flex: 1;
  border: none;
  outline: none;
  padding: 8px;
  font-size: 14px;
  background: transparent;
}

body[frappe-session-status="logged-out"][data-path="login"] .email-field{
  margin-top: 40px !important;
}

body[frappe-session-status="logged-out"][data-path="login"] .email-field .icon,
body[frappe-session-status="logged-out"][data-path="login"] .password-field .icon {
  margin-right: 8px !important;
  color: #666;
  flex-shrink: 0 !important;
}


body[frappe-session-status="logged-out"][data-path="login"] .password-field .toggle-password {
  margin-left: 8px !important;
  font-size: 14px;
  cursor: pointer;
  color: #007bff;
  flex-shrink: 0 !important;
  position: absolute;
  right: 5px;
}

body[frappe-session-status="logged-out"][data-path="login"] .page-card-body{
  position: relative !important;
}


body[frappe-session-status="logged-out"][data-path="login"] .forgot-password-message > a{
  display: flex !important;
  justify-content: end !important;
  color: #1E4AE9 !important;
}

body[frappe-session-status="logged-out"][data-path="login"] .page-card-actions > button{
  background-color: #05A8CC !important;
  color: white !important;
  width:100% !important;
  margin-inline: auto !important;
  padding-block: 10px !important;
      margin-block: 1.5em;
}

body[frappe-session-status="logged-out"][data-path="login"] .email-field >svg,
body[frappe-session-status="logged-out"][data-path="login"] .password-field > svg{
  display: none !important;
}

body[frappe-session-status="logged-out"][data-path="login"] .login-subtitle{
  /* text-align: start; */
  margin: 20px 0;
  /* font-weight: bold; */
  /* color: #333; */
}

